import React from "react";
import styled from "styled-components";
import { AnimateCSS, IAnimateCSS, animateName } from "../AnimateCSS";

const HeadShakeStyled = styled(AnimateCSS)`
  @keyframes headShake {
    0% {
      transform: translateX(0);
    }

    6.5% {
      transform: translateX(-6px) rotateY(-9deg);
    }

    18.5% {
      transform: translateX(5px) rotateY(7deg);
    }

    31.5% {
      transform: translateX(-3px) rotateY(-5deg);
    }

    43.5% {
      transform: translateX(2px) rotateY(3deg);
    }

    50% {
      transform: translateX(0);
    }
  }

  animation-name: ${p => animateName("headShake", p)};
  animation-timing-function: ease-in-out;
`;

const HeadShake = (props: IAnimateCSS) => <HeadShakeStyled {...props}>{props?.children}</HeadShakeStyled>;

export default HeadShake;
